import { ensureElement } from "../../utils/utils";
import { CardBase } from "../base/Cardbase";
const cardCategoryColor = {
    "кнопка": "card__category_button",
    "другое": "card__category_other",
    "софт-скил": "card__category_soft",
    "дополнительное": "card__category_additional",
    "хард-скил": "card__category_hard"
};
export class Card extends CardBase {
    constructor(container, actions) {
        super(container);
        this.cardCategory = ensureElement('.card__category', this.container);
        this.cardImg = ensureElement('.card__image', this.container);
        if (actions === null || actions === void 0 ? void 0 : actions.onClick) {
            container.addEventListener('click', actions.onClick);
        }
    }
    // устанавливаем категорию 
    set category(value) {
        this.setText(this.cardCategory, value);
        this.cardCategory.classList.add(cardCategoryColor[value]);
    }
    // устанавливаем картинку 
    set image(value) {
        this.setImage(this.cardImg, value);
    }
}
